import React, { useRef, useState, useEffect } from 'react';
import Webcam from 'react-webcam';
import "bootstrap/dist/css/bootstrap.min.css";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import './WebcamCapture.css'
import Loader from '../Loader/Loader';
import { useNavigate } from 'react-router-dom';

const WebcamCapture = ({ onCapture }) => {
  const webcamRef = useRef(null);
  const [capturing, setCapturing] = useState(false);
  const [progress, setProgress] = useState(0); 
  const navigate = useNavigate();
  const searchParams = new URLSearchParams(window.location.search);
  const userId = searchParams.get('userid');

  const [loader, setLoader] = useState(false)

  useEffect(() => {
    if (capturing) {
      capture();
    }
  }, [capturing]);

  const cancel = async () => {
    navigate(`/?userid=${userId}`)
  }

  const capture = async () => {
    const images = [];

    try {
      for (let i = 0; i < 900; i++) { // Capture 900 frames
        const imageSrc = webcamRef.current.getScreenshot();

        if (!imageSrc) {
          throw new Error('Failed to capture image');
        }

        images.push(imageSrc);
        setProgress(prev => prev + 1); // Update progress

        if (images.length === 100 || i === 899) { // Send images in batches of 100 or when reaching 900 frames
          onCapture(images); // Send batch of images to the parent component
          images.length = 0; // Clear captured images from memory
        }

        await new Promise(resolve => setTimeout(resolve, 1000 / 30)); // Wait for 1/30th of a second (30 FPS)
      }

      setCapturing(false); // Stop capturing after 900 frames

    } catch (error) {
      console.error('Capture error:', error);
      setCapturing(false);
      setProgress(0); // Reset progress
      images.length = 0; // Clear captured images from memory
    }
  };

  const startCapture = () => {
    setCapturing(true);
    setLoader(true)
  };

  const videoConstraints = {
    facingMode: "user",
    width: 1280,
    height: 720,
  };

  const handleVideoStream = (stream) => {
    const video = webcamRef.current.video;
    if (video) {
      video.style.transform = 'scaleX(-1)'; // Flip the video horizontally
    }
  };

  return (<>
   <>
   { !(loader && !capturing)?<> <Webcam
        className="webcam"
        audio={false}
        ref={webcamRef}
        screenshotFormat="image/jpeg"
        videoConstraints={videoConstraints}
        onUserMedia={handleVideoStream}
      />
      <div className="parent-container">
        <div className="instructions-div">
          <div className="instructions-back-btn" onClick={cancel}>
            <ArrowBackIosIcon />
          </div>
          <div style={{ border: "0px solid blue" }}>Instructions</div>
        </div>
        {!capturing ? (
          <button
            className="shadow btn start-btn"
            style={{
              display: "flex",
              flexDirection: "column",
              alignSelf: "center",
              border: "0px solid green",
              textAlign: "center",
              zIndex: 2,
              borderRadius: 20,
              padding: 15,
              position: "absolute",
              bottom: "5vh",
              backgroundColor: "white",
            }}
            onClick={startCapture}
            disabled={capturing}
          >
            Start Scan
          </button>
        ) : (
          <div
            className="shadow progress-card"
            
            style={{
              display: "flex",
              flexDirection: "column",
              alignSelf: "center",
              border: "0px solid green",
            //   width: "30vw",
              zIndex: 2,
              borderRadius: 20,
              padding: 15,
              position: "absolute",
              bottom: "5vh",
              opacity: 0.7,
              backgroundColor: "white",
            }}
          >
            <div style={{ border: "0px solid red", fontWeight: 600 }}>
              {Math.floor((progress / 900) * 100)}% Completed
            </div>
            <div style={{ border: "0px solid red" }}>Scan in progress...</div>
            <div
              style={{
                display: "flex",
                border: "0px solid red",
                color: "red",
                justifyContent: "right",
              }}
            >
              <div style={{ border: "0px solid yellow" }}>
                <ArrowBackIosIcon />
              </div>
              <div style={{ border: "0px solid yellow" }} onClick={cancel}>Cancel scan</div>
            </div>
          </div>
        )}
      </div></>:<Loader/>}
   </>
  
  </>
  
  );
};

export default WebcamCapture;

