import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import WebcamCapture from '../../Components/WebcamCapture/WebcamCapture';
import io from 'socket.io-client';

const FaceScan = () => {
  const [successMessage, setSuccessMessage] = useState('');
  const navigate = useNavigate();
  const searchParams = new URLSearchParams(window.location.search);
  const userId = searchParams.get('userid');
  const [socketClient, setSocketClient] = useState(null);
  const [start, setStart] = useState(false);

  useEffect(() => {
     const socket = io('https://face-scan-web.actofit.com:4000/', {
    //const socket = io('http://localhost:4000/', {
      transports: ['websocket'],
      reconnection: false,
      secure: true,
      rejectUnauthorized: true,
    });

    socket.on('connect', () => {
      console.log(`Connected to server with user ID: ${userId}`);
      socket.emit('setUserId', userId);
      setSocketClient(socket);
    });

    socket.on('disconnect', () => {
      console.log('Disconnected from server');
      navigate(`/try-again?userid=${userId}`);
    });

    socket.on('connect_error', (error) => {
      console.error('Connection error:', error);
      navigate(`/try-again?userid=${userId}`);
    });

    socket.on('success', (data) => {
      setSuccessMessage(data);
    //   console.log(data);
      navigate(`/result?data=${encodeURIComponent(JSON.stringify(data?.message))}&userid=${userId}`);
    });

    // WebSocket Heartbeat
    const heartbeatInterval = setInterval(() => {
       console.log(socket,"socketClient")
      if (socket) {
         console.log("not ok")
        socketClient?.emit('heartbeat', {}); // Send heartbeat message to server
      }
    }, 2000); // Send heartbeat every 5 seconds

    return () => {
      // Cleanup socket connection and heartbeat interval
      if (socketClient) {
        socketClient.disconnect();
      }
      clearInterval(heartbeatInterval);
    };
  }, [navigate, userId]);

  const handleCapture = (imageArray) => {
    setStart(!start);
    imageArray.forEach((imageSrc, index) => {
      const base64Image = imageSrc.split(',')[1];
      socketClient.emit('image', { userId, index, image: base64Image });
    });
  };

  return (
    <>
      <WebcamCapture onCapture={handleCapture} />
      {successMessage && <p>{successMessage}</p>}
    </>
  );
};

export default FaceScan;


